.header
    position: absolute
    width: 100%
    padding: 70px 0 50px 0
    background-color: transparent
    z-index: 5
    @include xl
        padding: 60px 0 40px 0
    @include lg
        padding: 50px 0 30px 0
    @include sm 
        padding: 40px 0 20px 0
    @include xxs
        padding: 30px 0 10px 0
    &__logo
        display: inline-block
        width: 160px
        @include lg
            width: 150px
        @include md
            width: 140px
        @include sm
            width: 130px
        @include xs
            width: 120px
        @include xxs
            width: 110px
        @include xxxs
            width: 100px
